<template>
  <div>
    <input type="file" @change="pickImageChange">
    <button @click="uploadImage">Tlcharger</button>
  </div>
</template>


<script>
import axios from 'axios';
import { API_BASE_URL } from '@/config.js';

export default {
  data() {
    return {
      takeFile: null
    };
  },
  methods: {
    pickImageChange(event) {
      this.takeFile = event.target.files[0];
    },
    async uploadImage() {
      if (!this.takeFile) {
        console.error('Aucun fichier slectionn.');
        return;
      }

      const childId = this.$route.params.id; // Rcuprer l'ID de l'enfant depuis les paramtres de la route
      const formDatas = new FormData();
      formDatas.append('profilePicture', this.takeFile);

      const token = localStorage.getItem('token'); // Rcuprer le jeton depuis le stockage local
      const headers = {
        'Content-Type': 'multipart/form-data',
        'Authorization': `Bearer ${token}` // Inclure le jeton dans l'en-tte Authorization
      };

      try {
        console.log('formData', formDatas);
        const response = await axios.post(`${API_BASE_URL}/childProfile/uploadChild/${childId}`, formDatas, { headers });
        localStorage.setItem('profilePhotoURL', response.data.path);
        console.log('Image tlcharge avec succs.', response.data);
      } catch (error) {
        console.error('Erreur lors du tlchargement de l\'image :', error);
      }
    }
  }
}
</script>
